<template>
    <div style="min-width: 1500px;">
        <div class="nav-container" ref="navContainer"></div>
        <div class="root">
            <div class="book wow fadeInLeft">
                <div class="cover">
                    <span>本草纲目</span>
                </div>
                <div class="content1"></div>
                <div class="content2"></div>
                <div class="content3"></div>

                <div class="content"  v-for="item,index in herbList" :key="index"  v-show="index === activeIndex">
                    <div v-html="item.content" class="article-content"></div>
                    <div v-show="herbListLength>1" class="prev" @click="prevBtn(index)"><i class="fa fa-angle-left" style="width: 100%;"></i></div>
                    <div v-show="herbListLength>1" class="next" @click="nextBtn(index)"><i class="fa fa-angle-right" style="width: 100%;"></i></div>
                </div>
                <div class="content3"></div>
            </div>
            <div class="search-container">
                <input v-model="input" @keyup.enter="searchFn()" type="text" placeholder="输入草药名称或您的症状 如：头痛、风湿等...">
                <button type="submit" @click.prevent="searchFn()"><i class="el-icon-search"></i></button>
                <div class="search-link">
                    未搜索到想要的中草药/药方？
                    <router-link to="aboutus">前往补充 -></router-link>
                </div>
                <div class="herbTypeList-container">
                    <div class="herbTypeList" @click="showHerbList('根茎类中药材')"><a>根茎类中草药</a></div>
                    <div class="herbTypeList" @click="showHerbList('果实/种子类中药材')"><a>果实/种子类中草药</a></div>
                    <div class="herbTypeList" @click="showHerbList('花类中药材')"><a>花类中草药</a></div>
                    <div class="herbTypeList" @click="showHerbList('枝叶/树皮类中药材')"><a>枝叶/树皮类中草药</a></div>
                    <div class="herbTypeList" @click="showHerbList('藤木类中药材')"><a>藤木类中草药</a></div>
                    <div class="herbTypeList" @click="showHerbList('全草类中药材')"><a>全草类中草药</a></div>
                    <div class="herbTypeList" @click="showHerbList('矿物类中药材')"><a>矿物类中草药</a></div>
                    <div class="herbTypeList" @click="showHerbList('动物类中药材')"><a>动物类中草药</a></div>
                    <div class="herbTypeList" @click="showHerbList('菌藻类中药材')"><a>菌藻类中草药</a></div>
                    <div class="herbTypeList" @click="showHerbList('其它中药材')"><a>其它中草药</a></div>
                    <transition name="fade">
                        <div class="herbTypeList-Info wow fadeInRight" v-show="listVisiable">
                            <h2 style="color: #614816;font-family:'xingkai';text-align: center;">{{this.herbtype}}</h2>
                            <span v-for="(item,index) in typeHerbList" :key="index" href="">{{item.herbname}}</span>
                        </div>
                    </transition>
                </div>
            </div>
        </div>
        <div class="chinaHerbMap">
            <ChinaMap :list = "MapList" ref="chinamap"></ChinaMap>
        </div>
    </div>
</template>

<script>
import {WOW} from 'wowjs'
import ChinaMap from '@/views/ChinaMap/index.vue'
export default {
    data(){
        return{
            herbList:[],
            typeHerbList:[],
            herbListLength: '',
            input:'',
            state:'',
            activeIndex: 0,
            herbtype: '',
            listVisiable: false,
            MapList:[]
        }
    },
    components:{ChinaMap},
    mounted(){
       this.$nextTick(() => {   //页面渲染完，在执行
	      let wow = new WOW({
	        live:false
	      })
	      wow.init()
	    })
    },
    methods:{
        searchFn(){
            this.$axios.post('/api/SearchHerbInfo',{input:this.input}).then(res=>{
                // console.log(res.data);
                if(res.status == 200){
                    this.herbList = res.data.data
                    // console.log(this.herbList);
                    let oldcity = res.data.data[0].city
                    let newcity = oldcity.split(',')
                    let city = newcity
                    console.log(city);
                    let oldvalue = res.data.data[0].value
                    let newvalue = oldvalue.split(',')
                    let value = newvalue
                    console.log(value);
                    let n = []
                    city.forEach((item,index)=>{
                        // n.push(MapList[index].substring(1,MapList[index].length-1))
                        n.city = city[index]
                        n.value = value[index]
                        n.push({'name':n.city,'value':+n.value})
                    })
                    // console.log(n.slice(0,5));
                    // console.log(n);
                    // let m = [...n]
                    // let i = Array.from(n)
                    // console.log(m);
                    // console.log(typeof(i));
                    this.$store.state.mapList = n.slice(0,n.length)
                    // console.log(this.$store.state.mapList);
                    this.herbListLength = res.data.data.length
                    this.$message({
                        type: 'success',
                        message: '已匹配到相关资料，请打开书本查看'
                    })
                    // this.$refs.chinamap.this.initMap()

                }else if(res.status == 201){
                    this.$message({
                        type: 'warning',
                        message: '抱歉，没有搜索到相关中药材！'
                    })
                }else{
                    this.$message({
                        type: 'danger',
                        message: '搜索失败，请稍后重试！'
                    })
                }
            })
        },
        prevBtn(){
            if(this.activeIndex != 0){
                this.activeIndex --
            }
        },
        nextBtn(){
            if(this.activeIndex < this.herbList.length - 1){
                this.activeIndex ++
            }
        },
        querySearch(queryString, cb) {
            var herbList = this.herbList;
            var results = queryString ? herbList.filter(this.createFilter(queryString)) : herbList;
            // 调用 callback 返回建议列表的数据
            cb(results);
        },
        createFilter(queryString) {
            return (herbList) => {
            return (herbList.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
            };
        },
        handleSelect(item) {
            console.log(item);
        },
        showHerbList(val){
            this.listVisiable = true;
            this.$axios.post('/api/getTypeHerb',{herbtype:val}).then(res=>{
                if(res.status == 200){
                    this.typeHerbList = res.data.data,
                    this.herbtype = res.data.data[0].herbtype
                }
            })
        },
    }
}
</script>

<style scoped>
